<!--
 * @Description: 编辑弹窗
 * @Autor: YML
 * @Date: 2021-05-14 11:53:32
 * @LastEditors: YML
 * @LastEditTime: 2021-05-20 16:06:35
-->
<template>
  <div class="edit-modal">
    <modal
      width="44.58vw"
      height="30.78vw"
      :title="title"
      @close="$emit('close')"
      :visible="visible"
    >
      <img
        slot="modalBg"
        class="img-edit"
        src="../../assets/img/components/img-edit2.png"
        alt=""
      />
      <a-form-model
        layout="inline"
        class="form-edit"
        ref="ruleForm"
        :model="form"
        :rules="rules"
      >
        <a-form-model-item label="勤务名称：" prop="dutyName">
          <a-select
            class="default-select"
            dropdown-class-name="default-select-dropdown"
            placeholder="请选择勤务名称"
            :disabled="isShow"
            :getPopupContainer="triggerNode => triggerNode.parentNode"
          >
            <a-select-option value="1">
              任务等级1
            </a-select-option>
            <a-select-option value="2">
              任务等级2
            </a-select-option>
          </a-select>
        </a-form-model-item>
        <a-form-model-item label="勤务级别：" prop="rank">
          <a-select
            class="default-select"
            dropdown-class-name="default-select-dropdown"
            placeholder="请选择勤务级别"
            :disabled="isShow"
            :getPopupContainer="triggerNode => triggerNode.parentNode"
          >
            <a-select-option value="1">
              任务等级1
            </a-select-option>
            <a-select-option value="2">
              任务等级2
            </a-select-option>
          </a-select>
        </a-form-model-item>
        <a-form-model-item label="勤务来源：" prop="source">
          <a-select
            class="default-select"
            dropdown-class-name="default-select-dropdown"
            placeholder="请选择勤务来源"
            :disabled="isShow"
            :getPopupContainer="triggerNode => triggerNode.parentNode"
          >
            <a-select-option value="1">
              检查站
            </a-select-option>
            <a-select-option value="2">
              检查站
            </a-select-option>
          </a-select>
        </a-form-model-item>
        <a-form-model-item label="日常默认：" prop="default">
          <a-select
            class="default-select"
            dropdown-class-name="default-select-dropdown"
            placeholder="请选择"
            :disabled="isShow"
            :getPopupContainer="triggerNode => triggerNode.parentNode"
          >
            <a-select-option value="1">
              日常默认1
            </a-select-option>
            <a-select-option value="2">
              日常默认2
            </a-select-option>
          </a-select>
        </a-form-model-item>
        <a-form-model-item label="启用状态：" prop="type">
          <a-select
            class="default-select"
            dropdown-class-name="default-select-dropdown"
            placeholder="请选择"
            :disabled="isShow"
            :getPopupContainer="triggerNode => triggerNode.parentNode"
          >
            <a-select-option value="1">
              已启用
            </a-select-option>
            <a-select-option value="2">
              未启用
            </a-select-option>
          </a-select>
        </a-form-model-item>
        <a-form-model-item label="开始时间：" prop="startTime">
          <a-date-picker
            class="default-date-picker"
            show-time
            dropdown-class-name="default-date-picker-dropdown"
            :disabled="isShow"
            :get-calendar-container="triggerNode => triggerNode.parentNode"
          />
        </a-form-model-item>
        <a-form-model-item label="结束时间：" prop="endTime">
          <a-date-picker
            class="default-date-picker"
            show-time
            dropdown-class-name="default-date-picker-dropdown"
            :disabled="isShow"
            :get-calendar-container="triggerNode => triggerNode.parentNode"
          />
        </a-form-model-item>
        <a-form-model-item label="备注">
          <a-textarea
            class="default-textarea"
            placeholder="请填写"
            :disabled="isShow"
          />
        </a-form-model-item>
      </a-form-model>
      <div class="line" v-show="!isShow"></div>
      <!-- 取消和保存按钮 start -->
      <div class="operation-btn" v-show="!isShow">
        <a-button type="default" class="form-button" @click="$emit('close')"
          >取消</a-button
        >
        <a-button type="primary" class="form-button save-button" @click="save">保存</a-button>
      </div>
      <!-- 取消和保存按钮 end -->
    </modal>
  </div>
</template>

<script>
import Modal from "../../components/Modal";
export default {
  components: {
    Modal //弹窗组件
  },
  props: {
    // 对话框是否可见
    visible: {
      type: Boolean,
      default: false
    },
    title: {
      type: String,
      default: ""
    }
  },
  data() {
    return {
      form: {
        dutyName: "",
        rank: "",
        source: "",
        default: "",
        type: "",
        startTime: "",
        endTime: ""
      },
      rules: {
        dutyName: [
          {
            required: true,
            message: "请选择勤务名称！",
            trigger: "blur"
          }
        ],
        rank: [
          {
            required: true,
            message: "请选择勤务级别！",
            trigger: "blur"
          }
        ],
        source: [
          {
            required: true,
            message: "请选择勤务来源！",
            trigger: "blur"
          }
        ],
        default: [
          {
            required: true,
            message: "请选择日常默认！",
            trigger: "blur"
          }
        ],
        type: [
          {
            required: true,
            message: "请选择启用状态！",
            trigger: "blur"
          }
        ],
        startTime: [
          {
            required: true,
            message: "请选择开始时间！",
            trigger: "blur"
          }
        ],
        endTime: [
          {
            required: true,
            message: "请选择结束时间！",
            trigger: "blur"
          }
        ]
      },
      isShow: this.title == "查看详情"
    };
  },
  methods: {
    save() {
      console.log(this.form);
      this.$refs.ruleForm.validate(valid => {
        if (valid) {
          alert("submit!");
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    }
  }
};
</script>

<style lang="less"></style>
